body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden{
    display: none

}

.modal-container {
    position: relative
}

.show {
    -webkit-animation: slideInLeft 1s ease-in;
            animation: slideInLeft 1s ease-in;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    left: 100%;
    position: absolute;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%
}

.no-show {
    -webkit-animation: slideOutLeft 1s ease-out;
            animation: slideOutLeft 1s ease-out;
    position: absolute;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%
}

@-webkit-keyframes slideInLeft {
    0% {
        left: 100%
    }
    100% {
        left: 0%
    }
}

@keyframes slideInLeft {
    0% {
        left: 100%
    }
    100% {
        left: 0%
    }
}

@-webkit-keyframes slideOutLeft {
    0% {
        right: 0%
    }
    100% {
        right: -100%
    }
}

@keyframes slideOutLeft {
    0% {
        right: 0%
    }
    100% {
        right: -100%
    }
}
.App {
  text-align: center;
  background-image: url("https://images.unsplash.com/photo-1527090526205-beaac8dc3c62?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1650&q=80");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
}

.test-css {
  color: #ffffff;
}

.test-css:hover {
  color: #26f7ff;
}
